'use client';

import { useClient } from '../contexts/ClientContext';
import { useChat, Message } from '../contexts/ChatContext';
import { useCallback, useEffect, useState } from 'react';

// Update the payload type to match LangGraph's expectations
interface StreamPayload {
  input: {
    messages: Message[];
  };
  streamMode?: Array<'events' | 'messages'>;
}

export function useChatActions() {
  const client = useClient();
  const { addMessage, setIsLoading, addEvent } = useChat();
  const [threadId, setThreadId] = useState<string | null>(null);
  const [assistantId, setAssistantId] = useState<string | null>(null);

  // Initialize thread and get default assistant on first load
  useEffect(() => {
    async function init() {
      if (!client) return;
      
      try {
        // Create a new thread
        const thread = await client.threads.create();
        setThreadId(thread.thread_id);

        // Get the first available assistant
        const assistants = await client.assistants.search();
        if (assistants.length > 0) {
          setAssistantId(assistants[0].assistant_id);
        }
      } catch (error) {
        console.error('Error initializing chat:', error);
      }
    }
    init();
  }, [client]);

  const sendMessage = useCallback(async (content: string) => {
    if (!content.trim() || !client || !threadId || !assistantId) return;

    // Add user message
    const userMessage: Message = {
      role: 'user',
      content,
    };
    addMessage(userMessage);
    setIsLoading(true);

    try {
      // Create the stream with the correct message format
      const stream = await client.runs.stream(threadId, assistantId, {
        input: {
          messages: [userMessage]
        },
        streamMode: ['events', 'messages']
      });

      // Process the stream
      for await (const chunk of stream) {
        addEvent(chunk);
      }
    } catch (error) {
      console.error('Error sending message:', error);
    } finally {
      setIsLoading(false);
    }
  }, [client, threadId, assistantId, addMessage, setIsLoading, addEvent]);

  return {
    sendMessage,
    ready: Boolean(client && threadId && assistantId)
  };
} 